import React from 'react';
import styled from 'styled-components';
import {getPostsProductoHistorico} from './../../utils/RestProductoHistorico'
import MaterialTableProductoHistorico from './MaterialTableProductoHistorico'

const Container = styled.div`
  max-width: 92%;
  margin-left: 7%;
  margin-top: 2%;
`;

class ProductoHistorico extends React.Component
{
    constructor(props){
        super(props);
        this.state =
        {
            posts: [],
            ProductoId : this.props.ProductoId,
            ProductoCodigoBarra : this.props.location.state,
        }
        console.log(this.state.ProductoCodigoBarra)
        console.log(props)
    }

    async componentDidMount() {

        /*Parametros en url [INICIO] */
        const params = new URLSearchParams(this.props.location.search);
        const tokenProductoId = params.get('ProductoId')
        this.setState({ProductoId: tokenProductoId})
        /*Parametros en url [FIN] */

        const ProductoId = tokenProductoId

        const ProductoHistoricoFiltros = {
            ProductoId : ProductoId,
        }

        await getPostsProductoHistorico(ProductoHistoricoFiltros)
        .then((res) => {
            this.setState({
                posts: res.data,
            })
        })
        .catch(reason => {
            console.log(reason)
        })
    }

    render(){
        const {posts, ProductoCodigoBarra} = this.state;

        return(
            <div>
                <Container>
                    <MaterialTableProductoHistorico
                    posts = {posts}
                    ProductoCodigoBarra = {ProductoCodigoBarra}
                    ></MaterialTableProductoHistorico>
                </Container>
            </div>
        )
    }
}

export default ProductoHistorico;