import React from 'react';
import {Link} from 'react-router-dom';
import 'react-pro-sidebar/dist/css/styles.css';
import { ProSidebar, Menu, MenuItem, SubMenu, SidebarHeader, SidebarContent, SidebarFooter } from 'react-pro-sidebar';
import { AiTwotoneFolderOpen,  FaHeart, FaHistory, GiHamburgerMenu} from "react-icons/fa";
import { ImMenu, ImFolderOpen, ImUsers, ImList2, ImDropbox, ImExit } from "react-icons/im";


function MenuProSidebar(){

    const [showMenu, setShowMenu] = React.useState(true)

  function ShowHiddeMenu() {

    {showMenu ? setShowMenu(false) :  setShowMenu(true)}
    
  }
  return (

      <div className = "MenuLeft">
      <ProSidebar collapsed = {showMenu}
        image = "https://bioalimentar.com/newpage/wp-content/uploads/2018/12/LaboratorioHeader.jpg?id=4635&45dd30&45dd30"
      >
  

  <SidebarHeader>
     {/* <div style = {{padding:"24px", fontWeight:"bold", fontSize:"14px", letterSpacing:"1px", textOverflow:"ellipsis", whiteSpace:"nowrap"}}>STOCK</div> */}
     <ImMenu style = {{padding:"24px", fontWeight:"bold", fontSize:"14px", letterSpacing:"1px", textOverflow:"ellipsis", whiteSpace:"nowrap"}}
     onClick = {ShowHiddeMenu}></ImMenu>
  </SidebarHeader>
  <SidebarContent>

  <Menu iconShape="square">
  {/* <Link to = "/Kardex"> */}
    <MenuItem icon={<FaHistory />}><Link to = "/Kardex">Inventario</Link></MenuItem>
  {/* </Link> */}

  {/* <Link to = "/CatalogoPedidos"> */}
    <MenuItem icon={<ImFolderOpen />}><Link to = "/CatalogoPedidos">Pedidos</Link></MenuItem>
{/* </Link> */}

    <MenuItem icon={<ImDropbox />}><Link to = "/CatalogoProducto">Productos</Link></MenuItem>
    {/* <Link to = "/CatalogoProveedores"> */}
    <MenuItem icon={<ImUsers />}><Link to = "/CatalogoProveedores">Proveedores</Link></MenuItem>
    {/* </Link> */}
    
   
    
    <SubMenu title="Catálogos" icon={<ImList2 />}>
      <MenuItem ><Link to = "/CatalogoAlmacenes">Almacén</Link></MenuItem>
      <MenuItem><Link to = "/CatalogoUnidadMedida">Unidad de Medida </Link></MenuItem>
      <MenuItem><Link to = "/CatalogoMotivoMovimiento">Tipo de movimientos</Link></MenuItem>
      <MenuItem><Link to = "/CatalogoClasificacion">Clasificación</Link></MenuItem>
      <MenuItem>Grupos</MenuItem>
    </SubMenu>
  </Menu>

    {/**
     *  You can add the content of the sidebar ex: menu, profile details, ...
     */}
  </SidebarContent>
  <SidebarFooter>
   <div>
   <ImExit style = {{padding:"24px", fontWeight:"bold", fontSize:"14px", letterSpacing:"1px", textOverflow:"ellipsis", whiteSpace:"nowrap"}}
    ></ImExit>
   </div>
    {/**
     *  You can add a footer for the sidebar ex: copyright
     */}
  </SidebarFooter>
</ProSidebar>
      </div>
       

  );
}

export default MenuProSidebar
